
<!--  -->
<template>
<div class='wrapper'>
    <v-head></v-head>
  <div class="clear"></div>
 <div class="el-main">
     <div id="infoform">
<el-form id="form1" ref="form1" :inline="true" :model="form1" :rules="rule1" label-width="100px" >
    <el-row style="width:1400px">   
    <el-form-item label="姓名：" prop="pname">
        <el-input v-model="form1.pname" style="width:100%" ></el-input>
     </el-form-item>
   </el-row>
    <el-row style="width:1400px">  
    <el-form-item label="学院编号：" prop="pdepID">
        <el-input v-model="form1.pdepID"  style="width:100%"></el-input>
     </el-form-item>
 </el-row>
  <el-row style="width:1400px">  
        	
    <el-form-item label="起聘年份：" prop="ptime">
        <el-input v-model="form1.ptime" style="width:100%"></el-input>
     </el-form-item>
  </el-row>
	 <el-col></el-col>
    </el-form>
    <el-form id="form2" :inline="true">
    <el-form-item>
       <el-button type="primary" @click="modifyForm('form1')">保存</el-button>
                 <el-button type="success" @click="resetForm('form1')">重置</el-button>
                 <el-button @click="exit('form1')">返回</el-button>
     </el-form-item>
   
     </el-form>
        </div>
    </div>
</div>

</template>
    
<script>
  import vHead from '../commons/AdminstratorNav.vue';
  export default {
     components:{
      vHead,
     },
      data: function(){
      return {
          hostUrl:'/teacher',
          pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          }
        },
          form1:{
             
              pname: '',
              pdepID:'',
              ptime: '',
              
          },
          rule1:{
             
          pname: [
            { required: true, message: '输入名字', trigger: 'blur' },
          ],
           pdepID: [
            { required: true, message: '请输入专业编号', trigger: 'blur' },
            { min: 12, max: 12, message: '长度为12个字符', trigger: 'blur' }
          ],
		  
		   ptime:[
             {  required: true, message: '请选择时间', trigger: 'change' },
         ]
		

          }
      }
    },
        methods: {

       modifyForm(formName) {
         var vm=this;
       $.ajax({
         type:'post',
         dataType:'json',
         url:'/administor/addProfessors',
         data:{
            "pname":this.form1.pname,
            "depID":this.form1.pdepID,
          
            "times":this.form1.ptime
         },
         success:function(response){
           if(response.data===1){
             vm.$message({
               type:'success',
               message:'添加成功'
             })
           }else{
             vm.$message({
               type:'info',
               message:'添加失败'
             })
           }
         },
         error:function(err){
           console.log(err);
           console.log("error"); 
           vm.$message({
               type:'warning',
               message:'网络故障'
             })
         }
       })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      exit(formName) {
        localStorage.setItem("firsts","1");
        this.$router.push('/adminstrator/teacherInfo')
      }
    }
  }
</script>
<style scoped>
.clear{
  clear: both;
}
.el-main{
    margin-top:3px;
    background-color:#eef1f6;
    color: #333;
    text-align: center;
    height:800px;
}
#infoform{
    padding-top: 60px;
}
#form1{
    width:400px;
    margin:10px auto;
}
#form2{
    width:220px;
    margin:0px auto;
    float: center;
}
  .el-form-item{
      float:left;
  }
</style>


